<template>
  <div class="index">
    <v-scroll
      :data="list"
      :pullDownCallBack="pullDown"
      :pullUpCallBack="pullUp"
      :pullUpLoad="true"
      :pullDownRefresh="true"
    >
      <h-scroll :data="tabList">
        <ul class="h">
          <li v-for="(item, index) in tabList" :key="index">{{item}}</li>
        </ul>
      </h-scroll>
      <ul class="v">
        <li v-for="(item, index) in list" :key="index">{{item}}</li>
      </ul>
      <select>
        <option value="1">1</option>
        <option value="2">2</option>
      </select>
      <router-link to="/test">测试</router-link>
    </v-scroll>
  </div>
</template>

<script>
import vscroll from "@/components/scroll/v-scroll";
import hscroll from "@/components/scroll/h-scroll";
export default {
  name: "index",
  components: {
    "v-scroll": vscroll,
    "h-scroll": hscroll
  },
  data() {
    return {
      list: [1, 2, 3, 4, 5, 6, 1, 2],
      tabList: ["热门", "女装", "美妆", "配饰", "女鞋", "母婴", "男装", "男鞋"]
    };
  },
  methods: {
    pullDown() {
      return this.$delay(2000).then(() => {
        this.list.unshift(...[1, 2, 3]);
      });
    },
    pullUp() {
      return this.$delay(2000).then(() => {
        this.list = this.list.concat([1, 2, 3]);
      });
    }
  },
  mounted() {}
};
</script>

<style lang="stylus">
.index {
  width: 100%;
  height: 736px;

  .h {
    line-height: 60px;
    white-space: nowrap;

    li {
      display: inline-block;
      margin-left: 10px;
      font-size: 0.2rem;
    }
  }

  .v {
    li {
      line-height: 60px;
      text-align: center;
      font-size: 0.2rem;
    }
  }
}
</style>


