<template>
  <div class="v-scroll">
    <cube-scroll
      ref="vscroll"
      :data="data"
      :options="options"
      @pulling-down="onPullingDown"
      @pulling-up="onPullingUp"
    >
      <slot></slot>
    </cube-scroll>
  </div>
</template>

<script>
export default {
  name: "v-scroll",
  props: [
    "data",
    "pullUpLoad",
    "pullDownRefresh",
    "pullDownCallBack",
    "pullUpCallBack"
  ],
  data() {
    return {
      list: [],
      options: {
        pullDownRefresh: false,
        pullUpLoad: false
      },
      secondStop: 40
    };
  },
  methods: {
    onPullingDown() {
      this.pullDownCallBack();
    },
    onPullingUp() {
      this.pullUpCallBack();
    }
  },
  beforeMount() {
    this.options.pullDownRefresh = this.pullDownRefresh && {
      threshold: 40,
      stopTime: 1000,
      txt: "更新成功"
    };
    this.options.pullUpLoad = this.pullUpLoad || false;
  }
};
</script>


<style lang="stylus">
.v-scroll {
  width: 100%;
  height: 100%;

  .after-trigger {
    flex: 1;
    margin: 0 auto;
    text-align: center;
    font-size: 0.14rem;
  }

  .cube-loading-spinners {
    margin: 0 auto;
  }
}
</style>

